import { FC, useState } from 'react';
import {
  Button,
  ButtonGroup,
  TopCard,
} from '../styles-suggestion-and-complaints';
import {
  Card,
  Cards,
  Description,
  FromToCard,
  InfoCard,
  Wrapper,
} from './styles-descended';

import new_icon from '@assets/images/dashboard/new.svg';
import profile from '@assets/images/dashboard/profile.svg';
import from_to from '@assets/images/dashboard/from-to.svg';
import calendar from '@assets/images/dashboard/dots-calendar.svg';
import watch from '@assets/images/dashboard/watch.svg';
import folder from '@assets/images/dashboard/blue-folder.svg';
import true_icon from '@assets/images/dashboard/true-icon.svg';
import trash from '@assets/images/dashboard/red-trash.svg';

const Descended: FC<{}> = () => {
  const [activeIndex, setActiveIndex] = useState<number>(0);
  return (
    <Wrapper>
      <TopCard>
        <h1>Mening tarixim</h1>
        <ButtonGroup justify>
          <Button
            background={activeIndex === 0 ? '#0496FF' : 'transparent'}
            color={activeIndex === 0 ? '#fff' : '#0496FF'}
            onClick={() => setActiveIndex(0)}
          >
            Takliflar 12
          </Button>

          <Button
            background={activeIndex === 2 ? '#F31B1B' : 'transparent'}
            color={activeIndex === 2 ? '#fff' : '#F31B1B'}
            onClick={() => setActiveIndex(2)}
          >
            Shikoyatlar 12
          </Button>
        </ButtonGroup>
      </TopCard>
      <Cards>
        <Card forComplaint>
          <div>
            <span>Жалоба #3 </span>
            <img src={new_icon} alt="yangi" />
            <FromToCard>
              <img src={profile} alt="profile" />
              <h2>Erkaboy Toirov</h2>
              <img src={from_to} alt="from_to" />
              <img src={profile} alt="profile" />
              <h2>Erkaboy Toirov</h2>
            </FromToCard>
            <InfoCard>
              <img src={calendar} alt="calendar" />
              <p>30.04.2022</p>
              <img src={watch} alt="watch" />
              <p>19:00</p>
              <img src={folder} alt="folder" />
              <p>2 ta fayl</p>
            </InfoCard>
          </div>
          <div>
            <Description>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit.
              Ullamcorper mauris diam risus odio ultrices. Quisque sed id mattis
              ullamcorper dolor enim suspendisse fames.
            </Description>
            {/* <BottomCard>
              <Button color="#ffffff" background="#0496FF">
                <img src={true_icon} /> Shikoyat qilish
              </Button>
              <Button color="#F31B1B" background="transparent">
                <img src={trash} /> O'chirish
              </Button>
            </BottomCard> */}
          </div>
        </Card>
      </Cards>
    </Wrapper>
  );
};

export default Descended;
