import styled from 'styled-components';

export const Wrapper = styled.div`
  width: 100%;
  margin-top: 40px;
  * {
    text-align: left !important ;
    box-shadow: none !important;
  }
  thead {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }
  tbody {
    .MuiTableCell-sizeMedium {
      font-weight: 600;
      font-size: 14px;
      line-height: 29px;
    }
    .MuiTableCell-alignRight {
      font-weight: 500;
      font-size: 14px;
      line-height: 29px;
      color: #81755c;
    }
    .MuiTableRow-root {
      border: 1px solid rgba(4, 150, 255, 0.1) !important;
      border-radius: 4px !important;
      margin-top: 10px !important;
      cursor: pointer;
      :hover {
        background: rgba(4, 150, 255, 0.1) !important;
      }
    }
  }
`;
