import { FC } from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Wrapper } from './styles-table';

function createData(
  description: string,
  whom: string,
  date: string,
  files: number
) {
  return { description, whom, date, files };
}

const rows = [
  createData(
    'Assalomu alaykum hurmatli HR bo’limi. O’z kuzatuvlarimdan kelib chiqib darslar...',
    'Habibjon Kodirov',
    '12.05.2021',
    24
  ),
  createData(
    'Assalomu alaykum hurmatli HR bo’limi. O’z kuzatuvlarimdan kelib chiqib darslar...',
    'Habibjon Kodirov',
    '12.05.2021',
    24
  ),
  createData(
    'Assalomu alaykum hurmatli HR bo’limi. O’z kuzatuvlarimdan kelib chiqib darslar...',
    'Habibjon Kodirov',
    '12.05.2021',
    24
  ),
  createData(
    'Assalomu alaykum hurmatli HR bo’limi. O’z kuzatuvlarimdan kelib chiqib darslar...',
    'Habibjon Kodirov',
    '12.05.2021',
    24
  ),
  createData(
    'Assalomu alaykum hurmatli HR bo’limi. O’z kuzatuvlarimdan kelib chiqib darslar...',
    'Habibjon Kodirov',
    '12.05.2021',
    24
  ),
  createData(
    'Assalomu alaykum hurmatli HR bo’limi. O’z kuzatuvlarimdan kelib chiqib darslar...',
    'Habibjon Kodirov',
    '12.05.2021',
    24
  ),
];

const AsTable: FC<{}> = () => {
  return (
    <Wrapper>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Izoh</TableCell>
              <TableCell align="right">Kimga</TableCell>
              <TableCell align="right">Sana</TableCell>
              <TableCell align="right">File</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                  {row.description}
                </TableCell>
                <TableCell align="right">{row.whom}</TableCell>
                <TableCell align="right">{row.date}</TableCell>
                <TableCell align="right">{row.files}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Wrapper>
  );
};

export default AsTable;
