import {
  Calendar,
  GeneralContainer,
  TaskOpening,
  UniversalModal,
} from '@components/common';
import { FC, useState } from 'react';
import {
  BodyCard,
  Button,
  ButtonGroup,
  Card,
  HeadCard,
  LeftBox,
  RightBox,
  TopCard,
  Wrapper,
} from './styles-suggestion-and-complaints';
import { SuggestionAndComplaintsProps } from './types-suggestion-and-complaints';
import suggestion from '@assets/images/dashboard/suggestion-complaints.svg';
import AsTable from './table/table';
import { HorizontalSeparator } from '@components/common/separators/sepatators';
import LeaveComplaint from './leave-complaint/leave-complaint';
import Descended from './descended/descended';

const SuggestionAndComplaints: FC<SuggestionAndComplaintsProps> = () => {
  const [activeIndex, setActiveIndex] = useState<number>(1);
  const [descended, setDescended] = useState<boolean>(true);
  const [modalType, setModalType] = useState('shikoyat');
  const [open, setOpen] = useState<boolean>(false);
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <GeneralContainer>
      {descended ? (
        <Descended />
      ) : (
        <Wrapper>
          <HeadCard>
            <RightBox>
              <h1>Taklif va shikoyatlar</h1>
              <Card>
                <div>
                  <p>
                    Xodim ustidan yokida kompaniya ustidan bironta taklif va
                    shikoyatlaringiz bo’lsa yozib qoldiring. Biz albbata ko’rib
                    chiqamiz.
                  </p>
                  <ButtonGroup>
                    <Button color="#0496FF" background="transparent">
                      Taklif yuborish
                    </Button>
                    <Button
                      color="#0496FF"
                      background="transparent"
                      onClick={() => {
                        setOpen(true);
                        setModalType('shikoyat');
                      }}
                    >
                      Shikoyat yuborish
                    </Button>
                  </ButtonGroup>
                </div>
                <div>
                  <img src={suggestion} alt="suggestion" />
                </div>
              </Card>
            </RightBox>
            <LeftBox>
              <Calendar />
            </LeftBox>
          </HeadCard>
          <BodyCard>
            <TopCard>
              <h1>Mening tarixim</h1>
              <ButtonGroup justify>
                <Button
                  background={activeIndex === 0 ? '#0496FF' : 'transparent'}
                  color={activeIndex === 0 ? '#fff' : '#0496FF'}
                  onClick={() => setActiveIndex(0)}
                >
                  Takliflar 12
                </Button>
                <Button
                  background={activeIndex === 1 ? '#FFBD26' : 'transparent'}
                  color={activeIndex === 1 ? '#fff' : '#FFBD26'}
                  onClick={() => setActiveIndex(1)}
                >
                  Shikoyatlar 12
                </Button>
                <Button
                  background={activeIndex === 2 ? '#F31B1B' : 'transparent'}
                  color={activeIndex === 2 ? '#fff' : '#F31B1B'}
                  onClick={() => setActiveIndex(2)}
                >
                  Qoniqtirilmagan 3
                </Button>
                <Button
                  background={activeIndex === 3 ? '#06D6A0' : 'transparent'}
                  color={activeIndex === 3 ? '#fff' : '#06D6A0'}
                  onClick={() => setActiveIndex(3)}
                >
                  Qoniqtirilgan 10
                </Button>
              </ButtonGroup>
            </TopCard>
            <AsTable />
          </BodyCard>
          <UniversalModal open={open} handleClose={handleClose}>
            {modalType === 'shikoyat' ? <LeaveComplaint /> : <TaskOpening />}
          </UniversalModal>
        </Wrapper>
      )}
    </GeneralContainer>
  );
};

export default SuggestionAndComplaints;
