import { FC } from "react";
import { FormControlLabel, Radio, TextareaAutosize } from "@mui/material";
import add_user from '@assets/images/dashboard/add-user-complaints.svg';
import plus from '@assets/images/dashboard/plus.svg';
import { Button, ButtonGroup, Card, Label, SelectCard, Title, Wrapper } from "./styles-leave-complaints";

const LeaveComplaint: FC<{}> = () =>{
    return (
      <Wrapper>
        <Title>Shikoyat qoldirish</Title>
        <Label>Kimning ustidan shikoyat?</Label>
        <Card>
          <SelectCard>
            <img src={add_user} alt="add_user" />
            <p>Hech kim tanlanmagan</p>
          </SelectCard>
          <SelectCard>
            <FormControlLabel
              value="company"
              control={<Radio />}
              label="Kompaniya ustida"
            />
          </SelectCard>
          <SelectCard>
            <img src={plus} alt="plus" />
            <h3>Hech kim tanlanmagan</h3>
          </SelectCard>
        </Card>
        <TextareaAutosize
          aria-label="minimum height"
          minRows={6}
          placeholder="Shikoyatingizni kiriting"
          style={{ width: 200 }}
        />
        <ButtonGroup>
          <Button> Bekor qilish</Button>
          <Button colored>Yuborish</Button>
        </ButtonGroup>
      </Wrapper>
    );
}
export default LeaveComplaint